/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography
html, body {
 font: 400 1.04rem/1.5 Avenir, Tahoma, "Open Sans", sans-serif;
 height: 100%;
 color: var(--global-text-color);
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 text-rendering: optimizeLegibility;
 word-break: break-word;
 text-justify: inter-word;
 hyphens: auto;
 text-underline-position: under;
 text-size-adjust: 100%;
 font-feature-settings: "kern" 1;
 font-kerning: normal;
 font-variant-ligatures: common-ligatures;
}

b, strong {
 font-weight: 500;
}

#footer {
 height: 10rem;
 width: 100%;
 margin: 5rem 0 0;
 clear: both;
 position: relative;
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table td {
 font-size: 1rem;
}

// Common link styles
span a, p a {
 color: var(--global-theme-color);

 &:hover {
 color: var(--global-hover-color);
 font-weight: 500;
 text-decoration: underline;
 }
}

.selected-papers {
 margin-top: 4rem;

 .paper {
 margin: 1.2rem 0;

 a {
 color: var(--global-theme-color);
 font-size: 1.05rem;
 font-weight: 500;

 &:hover {
 color: var(--global-hover-color);
 text-decoration: underline;
 }
 }

 .authors,
 .venue {
 font-size: 0.95rem;
 margin: 0;
 }

 .authors {
 color: var(--global-text-color-dark);
 }

 .venue {
 color: var(--global-text-color-lighter);
 }
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}

// Math & Captions
.equation {
 margin-bottom: 1rem;
 text-align: center;
}

.caption {
 font-size: 0.875rem;
 margin: 0.75rem 0 1.5rem;
 text-align: center;
}

// Card

.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 100%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation,
.citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 width: 100%;

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;

 p {
 display: inline-block;
 margin: 0;
 }
 }
}

.profile.float-right {
 margin-left: 1.5rem;
}

.profile.float-left {
 margin-right: 1.5rem;
}

@media (min-width: 576px) {
 .profile {
 width: 18%;

 .address {
 p {
 display: block;
 }
 }
 }
}

// Navbar customization
.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.95;

 .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);

 a:not(.active) {
 color: var(--global-text-color);
 }

 a:hover {
 color: var(--global-hover-color);
 }

 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
 }

 &.navbar-light {
 a:hover {
 text-decoration: none;
 }

 .navbar-brand {
 font-size: 1.8rem;
 padding: 0.2rem 0;
 font-weight: 600 !important;

 &.social {
 padding: 0;
 font-size: 1.7rem;

 a i::before {
 transition: all 0.2s ease-in-out;
 }

 a:hover i::before {
 color: var(--global-theme-color);
 }
 }
 }

 .navbar-nav .nav-item {
 .nav-link:hover {
 color: var(--global-hover-color);
 }

 &.active > .nav-link {
 background-color: inherit;
 font-weight: bolder;
 color: var(--global-theme-color);

 &:hover {
 color: var(--global-hover-color);
 }
 }
 }
 }
}

// Dropdown items
.dropdown-item:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }

 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }

 .middle-bar {
 opacity: 0;
 }

 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }

 .middle-bar {
 opacity: 1;
 }

 .bottom-bar {
 transform: rotate(0);
 }
}

#light-toggle {
 padding: 0;
 border: 0;
 background-color: inherit;
 color: var(--global-text-color);

 &:hover {
 color: var(--global-hover-color);
 }
}

// Social (bottom)
.social {
 padding: 3rem 0;
 text-align: center;
 background-color: #222;

 .contact-icon {
 font-size: 1rem;

 .contact-separator, h4 {
 color: #b3b3b3;
 }

 a {
 color: white;

 &:hover {
 color: var(--global-theme-color);
 }
 }
 }

 .contact-note {
 margin-top: 0.5rem;
 font-size: 1rem;
 color: #999;
 }
}

// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
 font-size: 0.75rem;

 .container {
 color: var(--global-footer-text-color);
 padding: 9px 0 8px;
 }

 a {
 color: var(--global-footer-link-color);

 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 padding-bottom: 0;
}

// CV

.cv {
 margin-bottom: 40px;

 .card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);

 .list-group-item {
 background-color: inherit;
 border-color: var(--global-divider-color);

 .badge {
 color: var(--global-card-bg-color) !important;
 background-color: var(--global-theme-color) !important;
 }
 }
 }
}

// Repositories

@media (min-width: 768px) {
 .repo {
 max-width: 50%;
 }
}

// Blog
.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding: 1rem;

 h1 {
 color: var(--global-theme-color);
 font-size: 1.5rem;
 }
}

.tag-list {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 1rem;

 ul {
 justify-content: center;
 display: flow-root;

 p, li {
 list-style: none;
 display: inline-block;
 padding: 1rem 0.5rem;
 color: var(--global-text-color-light);
 }
 }
}

.post-list {
 list-style-type: none;
 padding: 0;
 margin: 0;

 li {
 display: flex;
 justify-content: space-between;
 align-items: baseline;
 margin-bottom: 0.5rem;
 }

 h3 {
 margin: 0;
 font-size: 1em;
 font-weight: normal;
 }
}

.post-title {
 color: inherit;
 text-decoration: none;
 font-weight: 500;

 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }
}

.post-description-line {
 display: flex;
 align-items: baseline;
 gap: 0.5rem;
 color: var(--global-text-color-light);
 font-size: 0.9em;
}

.post-meta {
 color: var(--global-text-color-light);
 font-size: 0.9em;
 margin: 0;
 padding: 0;
 text-align: center;
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);

 &:hover {
 color: $black-color;
 }
 }

 &.active .page-link {
 color: $white-color;
 background-color: var(--global-theme-color);

 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 }
}

// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}

// Publications
.publications {
 margin-top: 1rem;

 .year {
 font-size: 2rem;
 color: $grey-color;
 margin-top: 1rem; // Reduce from default spacing
 margin-bottom: 0;
 text-align: center;
 }

 .bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 .row {
 margin-bottom: 1rem; // distance between publications
 margin-top: 1rem; // distance between publications
 }

 li {
 margin-bottom: 1rem;
 font-size: 0.95rem;

 .abbr {
 // space around publication badges.
 height: 1.5rem;
 margin-bottom: 0.1rem;
 }

 span {
 display: block;
 }

 .author {
 a {
 border-bottom: 1px dashed var(--global-theme-color);

 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }

 >em {
 border-bottom: 1px solid;
 font-style: normal;
 }
 }

 em.star {
 border-bottom: none;
 color: var(--global-theme-color);
 font-style: normal;
 }

 a.abstract.publink,
 a.bib.publink {
 color: var(--global-theme-color);
 cursor: pointer;

 &:hover {
 color: var(--global-text-color);
 background-color: white;
 text-decoration: underline;
 }
 }
 }
 }
}

// Collapsible Abstract
.publications .bibliography .publication-row {
 .abstract p {
 line-height: 1.4em;
 margin: 0.8em;
 font-size: 0.85rem;
 text-align: justify;
 }

 .bib pre {
 line-height: 1.4em;
 margin: 0.8em;
 font-size: 0.7rem;
 text-align: justify;
 }

 .abstract.hidden,
 .bib.hidden {
 max-height: 0;
 transition: all 0.5s ease-out;
 overflow: hidden;
 border-radius: 1rem;
 background-color: var(--global-hover-bg-color);
 }

 .abstract.hidden.open,
 .bib.hidden.open {
 max-height: 100em;
 transition-delay: 0.3s;
 transition: all 1s ease-in;
 padding: 0.3rem 0.6rem;
 margin-top: 0.3rem;
 }
}

// Badge
.publications ol.bibliography li .abbr .my-badge,
.my-badge {
 font-weight: 600;
 color: $white-color;
 background-color: $theme-color-dark;
 font-size: 0.8rem;
 border-radius: 0.2rem;
 display: inline-block;
 box-shadow: 0.15rem 0.15rem 0.2rem #aaaaaa;
 padding: 0.1rem 0.8rem;
}

// Rouge Color Customization
figure.highlight {
 margin: 0 0 1rem;
}

pre {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 6px;
 padding: 6px 12px;

 pre,
 code {
 background-color: transparent;
 border-radius: 0;
 padding: 0;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 3px;
 padding: 3px 3px;
}

// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: all 750ms !important;
 transition-delay: 0 !important;
}


progress {
 /* Positioning */
 position: fixed;
 left: 0;
 top: 56px;
 z-index: 10;

 /* Dimensions */
 width: 100%;
 height: 1.5px;

 /* Reset the appearance */
 -webkit-appearance: none;
 -moz-appearance: none;
 appearance: none;

 /* Get rid of the default border in Firefox/Opera. */
 border: none;

 /* Progress bar container for Firefox/IE10 */
 background-color: transparent;
}

progress::-webkit-progress-bar {
 background-color: #c5c5c5
}

progress::-webkit-progress-value {
 background-color: var(--global-theme-color);
}

progress::-moz-progress-bar {
 background-color: var(--global-theme-color);
}

.progress-container {
 width: 100%;
 background-color: transparent;
 position: fixed;
 top: 56px;
 left: 0;
 height: 5px;
 display: block;
}

.progress-bar {
 background-color: var(--global-theme-color);
 width: 0%;
 display: block;
 height: inherit;
}

// Blog
.post-header {
 margin-top: 5em;
 text-align: center;
 margin-bottom: 2em;
}

.post-title {
 font-size: 1em;
 margin-bottom: 0.2em;
 color: #333333;

 a {
 color: inherit;
 text-decoration: none;

 &:hover {
 color: var(--global-theme-color);
 }
 }
}

// Individual post page titles
.post-header h1.post-title {
 font-size: 2em; // Larger size for individual post pages
 margin-bottom: 0.2em;
 color: #333333;
}

.post {
 .post-description {
 color: var(--global-text-color-light);
 font-size: 0.9em;
 margin-bottom: 0.3em;
 }

 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.9em;
 margin-bottom: 0.3em;
 }
}

.post-content {
 p {
 margin-bottom: 1rem;
 }

 h1 { font-size: 1.8em; margin-bottom: 0.8em; }
 h2 { font-size: 1.5em; margin: 1.5em 0 0.6em; }
 h3 { font-size: 1.3em; margin-top: 1.2em; }
 h4, h5 { font-size: 1.1em; }
 h6 { font-size: 1em; }

 ul, ol {
 margin-left: 1rem;
 padding-left: 0;

 ul, ol {
 margin: 0.2rem 0 0.2rem 1rem;
 }
 }

 li {
 margin-bottom: 0.2rem;

 &:last-child {
 margin-bottom: 0;
 }
 }

 a {
 color: var(--global-theme-color);
 text-decoration: none;
 }

 em, i { font-style: italic; }
 strong, b { font-weight: 500; }
 del {
 text-decoration: line-through;
 color: var(--global-text-color-light);
 }

 blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1em 0;
 padding: 0.5em 1.25rem;
 font-size: 1rem;

 p {
 color: var(--global-text-color-light);
 }

 > :last-child {
 margin-bottom: 0;
 }
 }
}

.post-footer {
 margin-top: 2em;
 color: #999;
 padding-top: 1.5em;

 h3 {
 font-size: 1.6em;
 font-weight: 400;
 margin-bottom: 1em;
 }

 ul {
 list-style: none;
 padding: 0;
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 gap: 1em;
 }

 li {
 background-color: #fcf9f9;
 border: 1.2px solid #e9e9e9;
 padding: 1em;
 margin: 0;

 &:hover a {
 color: $theme-color;
 }
 }

 a {
 color: #333;
 text-decoration: none;
 display: block;
 font-weight: 500;
 margin-bottom: 0.5em;
 }

 p {
 font-size: 0.9em;
 color: #666;
 margin: 0;
 }

 span {
 color: #999;
 }
}

// Make root page titles bolder
.home .post-list h3 {
 font-weight: bold;
}
